/**
 * @file variableInjector.js
 * @description 
 *
 * ****
 * -  JS-Slash-Runner ()  `TavernHelper.updateVariablesWith()` API
 * -  **chat **`type: 'chat'`
 * -  ** JSON **  `vs_stat_data` 
 *
 * ****
 * - `vs_stat_data` MVU  stat_data  SAM  SAM_data
 *
 * ****
 * ```
 * {{vs_stat_data}}
 * ```
 *
 * ****
 * - MVU:  `{{stat_data}}`  JSON
 * - SAM:  `{{SAM_data}}`  JSON
 * - :  `{{vs_stat_data}}`  JSON
 *
 * @module events/variableInjector
 */

const MODULE_NAME = "[ST-VarSystemExtension/variableInjector]";

/**
 *  TavernHelper API 
 * @returns {boolean} true  TavernHelper.updateVariablesWith 
 */
function isTavernHelperAvailable() {
  return (
    typeof window !== "undefined" &&
    window.TavernHelper &&
    typeof window.TavernHelper.updateVariablesWith === "function"
  );
}

/**
 * 
 *
 * @param {Object} snapshot -  { hp: 100, mp: 50, location: "" }
 * @returns {Promise<void>}
 *
 * @example
 * await injectSnapshotVariables({ hp: 100, mp: 50, location: "" });
 * // : {{vs_stat_data}}
 */
export async function injectSnapshotVariables(snapshot) {
  if (!snapshot || typeof snapshot !== "object") {
    console.warn(MODULE_NAME, ":", snapshot);
    return;
  }

  //  API
  if (!isTavernHelperAvailable()) {
    console.error(
      MODULE_NAME,
      " (JS-Slash-Runner)  API ",
      " updateVariablesWith API",
      ": https://github.com/ShinoKana/JS-Slash-Runner",
    );
    return;
  }

  try {
    //  TavernHelper API  chat 
    await window.TavernHelper.updateVariablesWith(
      (variables) => {
        //  vs_stat_data 
        //  MVU  stat_data  SAM  SAM_data
        variables.vs_stat_data = snapshot;

        return variables;
      },
      { type: "chat" }, //  chat  global
    );

    console.log(MODULE_NAME, " vs_stat_data ");
  } catch (error) {
    console.error(MODULE_NAME, ":", error);
  }
}

/**
 * 
 *
 * @returns {Promise<Object|null>}  null
 */
export async function getCurrentSnapshotVariables() {
  if (!isTavernHelperAvailable()) {
    console.warn(MODULE_NAME, "");
    return null;
  }

  try {
    const variables = window.TavernHelper.getVariables({ type: "chat" });

    //  vs_stat_data
    return variables.vs_stat_data || null;
  } catch (error) {
    console.error(MODULE_NAME, ":", error);
    return null;
  }
}
