const PLUGIN_BASE_URL = "/api/plugins/var-manager/var-manager";

const snapshotsState = {
  snapshots: [],
  filteredSnapshots: [],
  searchQuery: "",
  selectedTag: "",
  loading: false,
  // 
  viewMode: "list", // "list" | "editor"
  editorController: null,
  editingSnapshotId: null, // null 
  draftSnapshot: null, // 
};

let snapshotButtons = null;
let snapshotEditorButtons = null;

/**
 *  API
 */
// CSRF token 
let cachedCsrfToken = null;

/**
 *  CSRF token
 */
async function getCsrfToken() {
  // 1.  SillyTavern 
  if (window.token || globalThis.token) {
    return window.token || globalThis.token;
  }

  // 2.  token
  if (cachedCsrfToken) {
    return cachedCsrfToken;
  }

  // 3.  token
  try {
    const response = await fetch("/csrf-token");
    if (response.ok) {
      const data = await response.json();
      cachedCsrfToken = data.token;
      return cachedCsrfToken;
    }
  } catch (error) {
    console.error(`${EXTENSION_LOG_PREFIX}  CSRF token :`, error);
  }

  return null;
}

/**
 *  MVU Schema 
 * -  $  $meta, $arrayMeta
 * -  $__META_EXTENSIBLE__$ 
 * @param {any} obj - 
 * @returns {any} 
 */
function stripMvuSchema(obj) {
  if (Array.isArray(obj)) {
    return obj
      .filter((item) => item !== "$__META_EXTENSIBLE__$")
      .map((item) => stripMvuSchema(item));
  } else if (typeof obj === 'object' && obj !== null && !(obj instanceof Date)) {
    const result = {};
    for (const key in obj) {
      //  $ 
      if (!key.startsWith('$')) {
        result[key] = stripMvuSchema(obj[key]);
      }
    }
    return result;
  }
  return obj;
}

/**
 *  JSON 
 * @param {any} data - 
 * @param {string} filename - 
 */
function exportJSONToFile(data, filename) {
  const json = JSON.stringify(data, null, 2);
  const blob = new Blob([json], { type: 'application/json' });
  const url = URL.createObjectURL(blob);
  const a = document.createElement('a');
  a.href = url;
  a.download = filename;
  a.click();
  URL.revokeObjectURL(url);
}

/**
 *  JSON
 * @returns {Promise<any|null>}  JSON  null
 */
async function importJSONFromFile() {
  return new Promise((resolve) => {
    const input = document.createElement('input');
    input.type = 'file';
    input.accept = '.json';
    input.onchange = async (e) => {
      const file = e.target.files?.[0];
      if (!file) {
        resolve(null);
        return;
      }
      try {
        const text = await file.text();
        const data = JSON.parse(text);
        resolve(data);
      } catch (error) {
        toastr.error(`${error.message}`);
        resolve(null);
      }
    };
    input.click();
  });
}

/**
 * 
 * { metadata: {...}, variables: {...} }
 * { ... }
 * @param {Object} snapshotBody - 
 * @param {boolean} stripSchema -  schemaMVU 
 * @returns {Object} 
 */
function normalizeSnapshotBody(snapshotBody, stripSchema = false) {
  if (!snapshotBody || typeof snapshotBody !== "object") {
    return {};
  }

  //  metadata  variables 
  const hasMetadata = "metadata" in snapshotBody;
  const hasVariables = "variables" in snapshotBody;

  if (hasMetadata && hasVariables) {
    console.log(
      `${EXTENSION_LOG_PREFIX}  variables `,
    );
    const result = snapshotBody.variables || {};

    // MVU  schema
    if (stripSchema && typeof result === 'object') {
      return stripMvuSchema(result);
    }

    return result;
  }

  // MVU  schema schema
  if (stripSchema && typeof snapshotBody === 'object') {
    return stripMvuSchema(snapshotBody);
  }

  // 
  return snapshotBody;
}

async function callPluginAPI(endpoint, options = {}) {
  const url = `${PLUGIN_BASE_URL}${endpoint}`;

  //  SillyTavern  getRequestHeaders()  headers CSRF token
  const standardHeaders = getRequestHeaders();

  const finalHeaders = {
    ...standardHeaders,
    "Content-Type": "application/json",
    ...options.headers,
  };

  try {
    const response = await fetch(url, {
      ...options,
      headers: finalHeaders,
    });

    if (!response.ok) {
      if (response.status === 404) {
        throw new Error("");
      }
      throw new Error(
        `API : ${response.status} ${response.statusText}`,
      );
    }

    if (response.status === 204) {
      return null;
    }

    return await response.json();
  } catch (error) {
    console.error(`${EXTENSION_LOG_PREFIX} API :`, endpoint, error);
    throw error;
  }
}

