import { isAIMessage } from "./messageUtils";
import { getSnapshotId, loadSnapshot } from "./snapshotIdentifier";

/**
 * ( AI )
 * @param {number} startMessageId - 
 * @param {Array} chat - 
 * @returns {Promise<Object|null>}  {anchorMessageId, snapshotId, snapshot}  null
 */
export async function findSnapshotAnchor(startMessageId, chat) {
  if (
    !Array.isArray(chat) ||
    startMessageId < 0 ||
    startMessageId >= chat.length
  ) {
    console.error("[ST-VarSystemExtension] findSnapshotAnchor: ", {
      startMessageId,
      chatLength: chat?.length,
    });
    return null;
  }

  //  startMessageId 
  for (let i = startMessageId; i >= 0; i--) {
    const message = chat[i];

    //  AI 
    if (!isAIMessage(message)) {
      continue;
    }

    // 
    const snapshotId = getSnapshotId(message);
    if (!snapshotId) {
      continue;
    }

    // 
    console.log(
      `[ST-VarSystemExtension] :  ${i} , ID: ${snapshotId}`,
    );
    const record = await loadSnapshot(snapshotId);

    if (!record) {
      // ()
      console.warn(
        `[ST-VarSystemExtension] : ${snapshotId}, `,
      );
      // ,
      // :  clearSnapshotId,
      continue;
    }

    // 
    return {
      anchorMessageId: i,
      snapshotId,
      snapshot: record.payload, //  payload  JSON
    };
  }

  // ,
  console.log("[ST-VarSystemExtension] ,");
  return null;
}

/**
 *  AI 
 * @param {number} startId - ()
 * @param {number} endId - ()
 * @param {Array} chat - 
 * @returns {Array<Object>} AI , {messageId, message, content}
 */
export function getAIMessageRange(startId, endId, chat) {
  if (!Array.isArray(chat)) {
    console.error("[ST-VarSystemExtension] getAIMessageRange: chat ");
    return [];
  }

  if (startId < 0 || endId >= chat.length || startId > endId) {
    console.error("[ST-VarSystemExtension] getAIMessageRange: ", {
      startId,
      endId,
      chatLength: chat.length,
    });
    return [];
  }

  const result = [];

  for (let i = startId; i <= endId; i++) {
    const message = chat[i];

    //  AI 
    if (!isAIMessage(message)) {
      continue;
    }

    //  swipe 
    const swipeId = message.swipe_id ?? 0;
    const content = Array.isArray(message.swipes)
      ? message.swipes[swipeId]
      : message.mes || "";

    result.push({
      messageId: i,
      message,
      content,
    });
  }

  return result;
}

/**
 *  AI 
 * ( null), 0 
 * @param {Object|null} anchor - , null
 * @param {number} targetMessageId - 
 * @param {Array} chat - 
 * @returns {Array<Object>} AI 
 */
export function getMessagesToProcess(anchor, targetMessageId, chat) {
  if (
    !Array.isArray(chat) ||
    targetMessageId < 0 ||
    targetMessageId >= chat.length
  ) {
    console.error("[ST-VarSystemExtension] getMessagesToProcess: ", {
      targetMessageId,
      chatLength: chat?.length,
    });
    return [];
  }

  // 
  let startId;
  if (anchor === null) {
    // , 0 
    startId = 0;
    console.log("[ST-VarSystemExtension]  0 ()");
  } else {
    // ()
    startId = anchor.anchorMessageId + 1;
    console.log(
      `[ST-VarSystemExtension]  ${startId} (: ${anchor.anchorMessageId})`,
    );
  }

  //  AI 
  return getAIMessageRange(startId, targetMessageId, chat);
}

/**
 *  AI 
 * 
 * @param {number} messageId - 
 * @param {Array} chat - 
 * @returns {Promise<Object|null>}  {snapshotId, snapshot}  null
 */
export async function checkLatestSnapshot(messageId, chat) {
  if (!Array.isArray(chat) || messageId < 0 || messageId >= chat.length) {
    return null;
  }

  const message = chat[messageId];

  //  AI 
  if (!isAIMessage(message)) {
    console.log(
      `[ST-VarSystemExtension]  ${messageId}  AI ,`,
    );
    return null;
  }

  // 
  const snapshotId = getSnapshotId(message);
  if (!snapshotId) {
    console.log(`[ST-VarSystemExtension]  ${messageId} `);
    return null;
  }

  // 
  console.log(
    `[ST-VarSystemExtension]  ${messageId} : ${snapshotId}`,
  );
  const record = await loadSnapshot(snapshotId);

  if (!record) {
    console.warn(
      `[ST-VarSystemExtension]  ${messageId} : ${snapshotId}`,
    );
    return null;
  }

  return {
    snapshotId,
    snapshot: record.payload,
  };
}
