/**
 * @file listeners.js
 * @description  SillyTavern 
 *
 * ****
 * - MESSAGE_RECEIVED: AI   
 * - MESSAGE_SWIPED:  swipe   swipe
 * - CHAT_CHANGED:   
 * - MESSAGE_DELETED:   
 * - CHAT_DELETED:   
 *
 * ****
 * -  `references/iframe/event.d.ts`  `tavern_events`
 * -  `ListenerType` 
 *
 * @module events/listeners
 */

import { processMessage, reprocessFromMessage } from "./processor";

const MODULE_NAME = "[ST-VarSystemExtension/listeners]";

//  SillyTavern  API
// 
import { saveChat, eventSource, event_types } from '@sillytavern/script';

// CSRF Token 
let cachedCsrfToken = null;

/**
 *  CSRF Token DELETE 
 * @returns {Promise<string|null>}
 */
async function getCsrfToken() {
  // 1. 
  if (window.token || globalThis.token) {
    return window.token || globalThis.token;
  }

  // 2. 
  if (cachedCsrfToken) {
    return cachedCsrfToken;
  }

  // 3. 
  try {
    const response = await fetch("/csrf-token");
    if (response.ok) {
      const data = await response.json();
      cachedCsrfToken = data.token;
      return cachedCsrfToken;
    }
  } catch (error) {
    console.error(MODULE_NAME, " CSRF token :", error);
  }

  return null;
}

/**
 * 
 * @returns {boolean} 
 */
function isVariableSystemEnabled() {
  try {
    const context = window.SillyTavern.getContext();
    const character = context.characters?.[context.characterId];

    return character?.data?.extensions?.st_var_system?.enabled === true;
  } catch (error) {
    console.error(MODULE_NAME, ":", error);
    return false;
  }
}

/**
 *  MESSAGE_RECEIVED 
 *
 * ****AI 
 * ****message_id (number) -  ID
 *
 * @param {number} messageId -  ID
 */
async function handleMessageReceived(messageId) {
  console.log(MODULE_NAME, `MESSAGE_RECEIVED:  #${messageId}`);

  if (!isVariableSystemEnabled()) {
    console.log(MODULE_NAME, "");
    return;
  }

  try {
    await processMessage(messageId);
  } catch (error) {
    console.error(MODULE_NAME, "MESSAGE_RECEIVED :", error);
  }
}

/**
 *  MESSAGE_SWIPED 
 *
 * **** swipe
 * ****message_id (number) -  swipe  ID
 *
 * ****SillyTavern  swipe  message_id
 *  swipe_id  swipe
 *
 * @param {number} messageId -  swipe  ID
 */
async function handleMessageSwiped(messageId) {
  console.log(MODULE_NAME, `MESSAGE_SWIPED:  #${messageId}`);

  if (!isVariableSystemEnabled()) {
    console.log(MODULE_NAME, "");
    return;
  }

  try {
    const context = window.SillyTavern.getContext();
    const message = context.chat[messageId];
    const swipeId = message?.swipe_id ?? null;

    console.log(MODULE_NAME, ` swipe ID: ${swipeId}`);

    await processMessage(messageId, swipeId);
  } catch (error) {
    console.error(MODULE_NAME, "MESSAGE_SWIPED :", error);
  }
}

/**
 *  CHAT_CHANGED 
 *
 * ****
 * ****chat_file_name (string) - 
 *
 * ****
 * 1. 
 * 2.  AI 
 * 3. 
 *
 * ****
 *
 * @param {string} chatFileName - 
 */
async function handleChatChanged(chatFileName) {
  console.log(MODULE_NAME, `CHAT_CHANGED: ${chatFileName}`);

  try {
    if (!isVariableSystemEnabled()) {
      console.log(MODULE_NAME, "");
      return;
    }

    //  AI 
    const context = window.SillyTavern.getContext();
    const chat = context.chat;

    if (!chat || chat.length === 0) {
      console.log(MODULE_NAME, "");
      return;
    }

    //  AI 
    for (let i = chat.length - 1; i >= 0; i--) {
      const message = chat[i];
      if (message.is_user === false || message.role === "assistant") {
        console.log(MODULE_NAME, ` AI : #${i}`);
        await processMessage(i);
        break;
      }
    }
  } catch (error) {
    console.error(MODULE_NAME, "CHAT_CHANGED :", error);
  }
}

/**
 *  MESSAGE_DELETED 
 *
 * ****
 * ****message_id (number) -  ID
 *
 * ****
 * - **** UUID 
 * - ****
 *
 * @param {number} deletedMessageId -  ID
 */
async function handleMessageDeleted(deletedMessageId) {
  console.log(MODULE_NAME, `MESSAGE_DELETED:  #${deletedMessageId}`);

  if (!isVariableSystemEnabled()) {
    console.log(MODULE_NAME, "");
    return;
  }

  try {
    const context = window.SillyTavern.getContext();
    const chat = context.chat;

    if (!chat || chat.length === 0) {
      console.log(MODULE_NAME, "");
      return;
    }

    // 
    //  ID  deletedMessageId 
    console.log(MODULE_NAME, ` #${deletedMessageId} `);
    await reprocessFromMessage(deletedMessageId);
  } catch (error) {
    console.error(MODULE_NAME, "MESSAGE_DELETED :", error);
  }
}

/**
 *  CHAT_DELETED 
 *
 * ****
 * ****chat_file_name (string) - 
 *
 * ****
 * -  DELETE /snapshots/by-chat/:chatFile 
 * -  chat_metadata
 *
 * @param {string} chatFileName - 
 */
async function handleChatDeleted(chatFileName) {
  console.log(MODULE_NAME, `CHAT_DELETED: ${chatFileName}`);

  try {
    // 
    if (!chatFileName) {
      console.warn(MODULE_NAME, "");
      return;
    }

    const csrfToken = await getCsrfToken();
    if (!csrfToken) {
      console.warn(MODULE_NAME, " CSRF Token");
      return;
    }

    const url = `/api/plugins/var-manager/var-manager/snapshots/by-chat/${encodeURIComponent(chatFileName)}`;

    const response = await fetch(url, {
      method: "DELETE",
      headers: {
        "X-CSRF-Token": csrfToken,
      },
    });

    if (response.status === 404) {
      console.log(MODULE_NAME, "");
      return;
    }

    if (!response.ok) {
      console.error(
        MODULE_NAME,
        ` (${response.status}): ${chatFileName}`,
      );
      return;
    }

    const result = await response.json();
    console.log(
      MODULE_NAME,
      ` "${chatFileName}"  ${result.deletedCount ?? 0} `,
    );
  } catch (error) {
    console.error(MODULE_NAME, "CHAT_DELETED :", error);
  }
}

/**
 * 
 *
 * ****
 */
export function registerEventListeners() {
  console.log(MODULE_NAME, "");

  //  eventSource.on  API
  eventSource.on(event_types.MESSAGE_RECEIVED, handleMessageReceived);
  eventSource.on(event_types.MESSAGE_SWIPED, handleMessageSwiped);
  eventSource.on(event_types.CHAT_CHANGED, handleChatChanged);
  eventSource.on(event_types.MESSAGE_DELETED, handleMessageDeleted);
  eventSource.on(event_types.CHAT_DELETED, handleChatDeleted);

  console.log(MODULE_NAME, "");
}

/**
 * 
 *
 * ****eventOn 
 * 
 */
export function unregisterEventListeners() {
  console.log(MODULE_NAME, "");

  eventSource.removeListener(event_types.MESSAGE_RECEIVED, handleMessageReceived);
  eventSource.removeListener(event_types.MESSAGE_SWIPED, handleMessageSwiped);
  eventSource.removeListener(event_types.CHAT_CHANGED, handleChatChanged);
  eventSource.removeListener(event_types.MESSAGE_DELETED, handleMessageDeleted);
  eventSource.removeListener(event_types.CHAT_DELETED, handleChatDeleted);

  console.log(MODULE_NAME, "");
}
