/**
 *  - 
 */

import { error, log, warn } from "../logger";

const EXTENSION_LOG_PREFIX = "[ST-VarSystemExtension]";

/**
 * 
 * @typedef {Object} ExecutionContext
 * @property {number} messageId -  ID
 * @property {string} messageContent - 
 * @property {string} characterName - 
 * @property {string} chatFile - 
 * @property {number} timestamp - 
 */

/**
 * 
 * @typedef {Object} ExecutionResult
 * @property {any} snapshot - 
 * @property {Array<{functionName: string, error: string}>} errors - 
 */

/**
 * 
 */
export class FunctionExecutor {
  constructor() {
    // 
    this.safeGlobals = {
      // Lodash ()
      _: window._,

      //  JavaScript 
      Object,
      Array,
      String,
      Number,
      Boolean,
      Math,
      Date,
      JSON,

      // 
      parseInt,
      parseFloat,
      isNaN,
      isFinite,

      //  console.log eval/Function
      console: {
        log: console.log.bind(console),
        warn: console.warn.bind(console),
        error: console.error.bind(console),
      },
    };
  }

  /**
   * 
   * @param {string} executorCode - 
   * @returns {Function|null}
   */
  compileExecutor(executorCode) {
    try {
      // 
      const params = [
        "snapshot",
        "args",
        "context",
        ...Object.keys(this.safeGlobals),
      ];

      //  Function 
      const func = new Function(...params, executorCode);

      return func;
    } catch (e) {
      error(`${EXTENSION_LOG_PREFIX} :`, e);
      return null;
    }
  }

  /**
   * 
   * @param {import('./registry.js').FunctionDefinition} functionDef - 
   * @param {any} snapshot - 
   * @param {Array<string>} args - 
   * @param {ExecutionContext} context - 
   * @returns {{success: boolean, snapshot: any, error?: string}}
   */
  executeFunction(functionDef, snapshot, args = [], context = {}) {
    try {
      // 
      const executor = this.compileExecutor(functionDef.executor);
      if (!executor) {
        return {
          success: false,
          snapshot,
          error: "",
        };
      }

      // 
      const snapshotCopy = JSON.parse(JSON.stringify(snapshot));

      // 
      const funcParams = [
        snapshotCopy,
        args,
        context,
        ...Object.values(this.safeGlobals),
      ];

      // 
      const result = executor(...funcParams);

      // 
      if (result === undefined || result === null) {
        warn(
          `${EXTENSION_LOG_PREFIX}  ${functionDef.name} `,
        );
        return {
          success: true,
          snapshot: snapshotCopy,
        };
      }

      return {
        success: true,
        snapshot: result,
      };
    } catch (e) {
      error(`${EXTENSION_LOG_PREFIX}  ${functionDef.name} :`, e);
      return {
        success: false,
        snapshot,
        error: e.message || String(e),
      };
    }
  }

  /**
   * 
   * @param {any} inputSnapshot -  AI 
   * @param {Array<import('./registry.js').ParsedFunctionCall>} activeCalls - 
   * @param {{before: Array, after: Array}} passiveFunctions - 
   * @param {ExecutionContext} context - 
   * @returns {ExecutionResult}
   */
  executeAll(inputSnapshot, activeCalls, passiveFunctions, context) {
    let currentSnapshot = JSON.parse(JSON.stringify(inputSnapshot));
    const errors = [];

    // 1. 
    log(
      `${EXTENSION_LOG_PREFIX}  ${passiveFunctions.before.length} `,
    );
    for (const func of passiveFunctions.before) {
      const result = this.executeFunction(func, currentSnapshot, [], context);
      if (result.success) {
        currentSnapshot = result.snapshot;
      } else {
        errors.push({
          functionName: func.name,
          error: result.error || "",
        });
      }
    }

    // 2. 
    log(`${EXTENSION_LOG_PREFIX}  ${activeCalls.length} `);
    for (const call of activeCalls) {
      const result = this.executeFunction(
        call.functionDef,
        currentSnapshot,
        call.args,
        context,
      );
      if (result.success) {
        currentSnapshot = result.snapshot;
      } else {
        errors.push({
          functionName: call.functionDef.name,
          error: result.error || "",
        });
      }
    }

    // 3. 
    log(
      `${EXTENSION_LOG_PREFIX}  ${passiveFunctions.after.length} `,
    );
    for (const func of passiveFunctions.after) {
      const result = this.executeFunction(func, currentSnapshot, [], context);
      if (result.success) {
        currentSnapshot = result.snapshot;
      } else {
        errors.push({
          functionName: func.name,
          error: result.error || "",
        });
      }
    }

    // 
    if (errors.length > 0) {
      warn(
        `${EXTENSION_LOG_PREFIX}  ${errors.length} :`,
        errors,
      );

      //  toastr 
      if (window.toastr) {
        window.toastr.warning(
          ` ${errors.length} `,
          "",
        );
      }
    }

    return {
      snapshot: currentSnapshot,
      errors,
    };
  }
}

/**
 * 
 */
export const functionExecutor = new FunctionExecutor();

/**
 * 
 * @param {Array<{functionDef, args, index, fullMatch}>} activeCalls - 
 * @param {any} inputSnapshot - 
 * @returns {Promise<any>} 
 */
export async function executeFunctionPipeline(activeCalls, inputSnapshot) {
  //  functionRegistry 
  const { functionRegistry } = await import('./registry.js');

  // 
  const passiveFunctions = functionRegistry.getPassiveFunctions();

  // 
  const context = {
    timestamp: Date.now(),
  };

  // 
  const result = functionExecutor.executeAll(
    inputSnapshot,
    activeCalls,
    passiveFunctions,
    context
  );

  // 
  return result.snapshot;
}
