/**
 * @file logger.js
 * @description 
 * 
 */

const PREFIX = '[ST-VarSystemExtension]';

/**
 * 
 * @param {...any} args - 
 */
export const log = (...args) => console.log(PREFIX, ...args);

/**
 * 
 * @param {...any} args - 
 */
export const warn = (...args) => console.warn(PREFIX, ...args);

/**
 * 
 * @param {...any} args - 
 */
export const error = (...args) => console.error(PREFIX, ...args);
