/**
 * 
 * 
 */

import { getRequestHeaders } from "@sillytavern/script";
import { getCurrentSwipeId } from "./messageUtils";

const SNAPSHOT_ID_KEY = "st_var_system_snapshot_id";

/**
 * (UUID v4)
 * @returns {string} UUID 
 */
export function generateSnapshotId() {
  //  crypto.randomUUID() ( API)
  // , ID
  if (
    typeof crypto !== "undefined" &&
    typeof crypto.randomUUID === "function"
  ) {
    return crypto.randomUUID();
  }

  // : ID
  return `${Date.now()}-${Math.random().toString(36).substring(2, 15)}`;
}

/**
 *  swipe 
 * @param {Object} message - 
 * @returns {string|null} , null
 */
export function getSnapshotId(message) {
  if (!message) {
    return null;
  }

  const swipeId = getCurrentSwipeId(message);

  //  swipes_info 
  if (!Array.isArray(message.swipes_info)) {
    return null;
  }

  const swipeInfo = message.swipes_info[swipeId];
  if (!swipeInfo) {
    return null;
  }

  // 
  const identifier = swipeInfo[SNAPSHOT_ID_KEY];
  return typeof identifier === "string" ? identifier : null;
}

/**
 * 
 * @param {Object} message - 
 * @param {string} snapshotId - 
 * @param {number|null} swipeId - swipe IDnull  swipe
 * @returns {boolean} 
 */
export function setSnapshotId(message, snapshotId, swipeId = null) {
  if (!message || typeof snapshotId !== "string") {
    return false;
  }

  //  swipeId swipe_id
  const targetSwipeId = swipeId !== null ? swipeId : getCurrentSwipeId(message);

  //  swipes_info 
  if (!Array.isArray(message.swipes_info)) {
    message.swipes_info = [];
  }

  //  swipe  info 
  if (!message.swipes_info[targetSwipeId]) {
    message.swipes_info[targetSwipeId] = {};
  }

  // 
  message.swipes_info[targetSwipeId][SNAPSHOT_ID_KEY] = snapshotId;
  return true;
}

/**
 * 
 * @param {Object} message - 
 * @returns {boolean} 
 */
export function clearSnapshotId(message) {
  if (!message) {
    return false;
  }

  const swipeId = getCurrentSwipeId(message);

  //  swipes_info 
  if (!Array.isArray(message.swipes_info) || !message.swipes_info[swipeId]) {
    return false;
  }

  // 
  delete message.swipes_info[swipeId][SNAPSHOT_ID_KEY];
  return true;
}

/**
 * 
 * @param {Object} message - 
 * @returns {boolean} 
 */
export function hasSnapshotId(message) {
  return getSnapshotId(message) !== null;
}

/**
 * 
 * @param {string} identifier - 
 * @returns {Promise<Object|null>} , null
 */
export async function loadSnapshot(identifier) {
  if (!identifier) {
    console.error("[ST-VarSystemExtension] loadSnapshot: identifier ");
    return null;
  }

  try {
    const response = await fetch(
      `/api/plugins/var-manager/var-manager/snapshots/${identifier}`,
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      },
    );

    if (!response.ok) {
      if (response.status === 404) {
        console.warn(`[ST-VarSystemExtension] : ${identifier}`);
        return null;
      }
      throw new Error(`HTTP ${response.status}: ${response.statusText}`);
    }

    const record = await response.json();
    return record;
  } catch (error) {
    console.error(`[ST-VarSystemExtension] : ${identifier}`, error);
    return null;
  }
}

/**
 * 
 * @param {Object} params - 
 * @param {string} params.chatFile - 
 * @param {number} params.messageId - 
 * @param {Object} params.payload - ()
 * @param {string} [params.identifier] - ()
 * @returns {Promise<Object|null>} , null
 */
export async function saveSnapshot(params) {
  const { chatFile, messageId, payload, identifier } = params;

  if (!chatFile || messageId === undefined || !payload) {
    console.error("[ST-VarSystemExtension] saveSnapshot: ", params);
    return null;
  }

  try {
    const response = await fetch(
      "/api/plugins/var-manager/var-manager/snapshots",
      {
        method: "POST",
        headers: {
          ...getRequestHeaders(),
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          chatFile,
          messageId: String(messageId),
          payload,
          identifier,
        }),
      },
    );

    if (!response.ok) {
      throw new Error(`HTTP ${response.status}: ${response.statusText}`);
    }

    const result = await response.json();
    return result;
  } catch (error) {
    console.error("[ST-VarSystemExtension] ", params, error);
    return null;
  }
}

/**
 * 
 *
 * 
 *
 * @param {number} messageId - 
 * @param {number|null} swipeId - swipe IDnull  swipe
 * @param {Object} snapshot - 
 * @returns {Promise<string|null>}  null
 */
export async function saveSnapshotToPlugin(messageId, swipeId, snapshot) {
  try {
    // 1. 
    const context = window.SillyTavern.getContext();
    const chatFile = context.getCurrentChatId?.() || context.chatId;

    if (!chatFile) {
      console.warn(
        "[ST-VarSystemExtension] saveSnapshotToPlugin: ",
      );
      return null;
    }

    const chat = context.chat;
    if (!chat || messageId >= chat.length) {
      console.error(
        "[ST-VarSystemExtension] saveSnapshotToPlugin:  ID ",
        { messageId, chatLength: chat?.length },
      );
      return null;
    }

    // 2. 
    const message = chat[messageId];
    if (!message) {
      console.error(
        "[ST-VarSystemExtension] saveSnapshotToPlugin: ",
        messageId,
      );
      return null;
    }

    // 3. 
    const identifier = generateSnapshotId();

    // 4. 
    const result = await saveSnapshot({
      chatFile,
      messageId,
      payload: snapshot,
      identifier,
    });

    if (!result) {
      // 
      console.warn(
        "[ST-VarSystemExtension] saveSnapshotToPlugin: ",
      );
      return null;
    }

    // 5. 
    //  swipeId  nullsetSnapshotId  swipe_id
    if (!setSnapshotId(message, identifier, swipeId)) {
      console.error(
        "[ST-VarSystemExtension] saveSnapshotToPlugin: ",
        { messageId, swipeId },
      );
      return null;
    }

    const actualSwipeId =
      swipeId !== null ? swipeId : getCurrentSwipeId(message);
    console.log(
      `[ST-VarSystemExtension] :  #${messageId}, swipe=${actualSwipeId}, ID=${identifier}`,
    );
    return identifier;
  } catch (error) {
    console.error("[ST-VarSystemExtension] saveSnapshotToPlugin :", error);
    return null;
  }
}
